<template>
  <!-- 发货信息 -->
  <div class="container">
    <el-tabs v-model="activeName">
      <el-tab-pane label="发货信息" name="deliveryInformation">
        <deliverydelails></deliverydelails>
      </el-tab-pane>
    </el-tabs>
    <div class="loginOut">
      <el-form-item>
        <el-button
          type="primary"
          size="mini"
          @click="saveBtn()"
          >保存</el-button
        >
        <el-button size="mini" @click="returnBtn()"
          >返回</el-button
        >
      </el-form-item>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import deliverydelails from "../delivery_management/deliverydelails.vue";
import { getCurrentInstance } from "@vue/runtime-core";
export default {
  components: {
    deliverydelails,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      activeName: "deliveryInformation",
    });
    const saveBtn = () => {
      proxy.$router.push("/delivery");
    };
    const returnBtn = () => {
      // proxy.$router.push("/delivery");
      proxy.$router.go(-1)
    };

    return {
      ...toRefs(state),
      saveBtn,
      returnBtn,
    };
  },
};
</script>

<style scoped>
.container {
  background-color: #fff;
}

.loginOut {
  position: absolute;
  right: 35px;
  top: 60px;
}
</style>