<script>
import { reactive, toRefs } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
import deliverydialog from "../delivery_management/deliverydialog.vue";
export default {
  components: {
    deliverydialog,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      quantity_delivered:"",
      deliver_subject: "",
      contractId: "",
      contractlist: [],
      customerId: "",
      customerlist: [],
      tracking_number: "",
      delivererId: "",
      delivererlist: [],
      deliver_number: "",
      remarks: "",
      deliver_date: "",
      flag: true,
      tableData: [
        {
          product_classification: "",
          quantity_delivered: "",
          deliverable_quantity: "",
          product_code: "",
          product_name: "",
          product_abbreviation: "",
          specification_model: "",
          unit: "",
          quantity: "",
          remarks: "",
          order_notes: "",
          unit_price: "",
          quantity_shipped: "",
          quantity_returned: "",
          amount_received: "",
          uncollected_amount: "",
          invoiced_amount: "",
          uninvoiced_amount: "",
          manufacturer: "",
          refunded_amount: "",
        },
      ],
    });

    const handleSelectionChange = (val) => {
      proxy.multipleSelection = val;
    };
    const addBtn = () => {
      proxy.$refs.deliverydialog.setedit();
    };
    const handleEdit = () => {
      proxy.flag = false;
    };
    const handleDel = () => {};
    const upload = () => {};

    return {
      ...toRefs(state),
      handleEdit,
      handleDel,
      upload,
      handleSelectionChange,
      addBtn,
    };
  },
};
</script>

